<p class="title mt30">一、我们收集您的个人信息</p>

<p>个人信息是指以电子或者其他方式记录的能够单独或者与其他信息结合识别特定自然人身份或者反映特定自然人活动情况的各种信息。</p>

<p>我们仅会出于本政策所述的以下目的，收集您的个人信息：</p>

<p>1、为了标记您为游族网络的用户以及用于账号发生归属纠纷的处理，如果您使用微信、QQ或微博账号关联登录游族游戏，我们会收集您微信、QQ或微博账号相应的唯一标识，（包括微信账号的昵称、头像、地区和性别，QQ账号的昵称、头像、性别，微博账号的个人信息、分享内容到你的微博的权限），用于保存您的登录信息，以便您在不同设备登录以及方便您分享游戏内容。 </p>

<p>2、为满足相关法律法规政策及相关主管部门的要求，游族网络游戏用户需进行实名认证以继续使用游戏服务。我们会在您主动提供的情况下收集您的实名身份信息，包括身份证信息、军官证信息、护照信息、手机号码，拒绝提供实名身份信息可能会导致您无法登陆游戏或在使用游族网络游戏服务过程中受到相应限制。</p>

<p>3、为保障您正常使用游族网络的游戏服务，确保游戏服务功能的正常运行，优化游戏产品体验，保障您的账号安全，在您注册或使用游族网络游戏服务时，我们会收集您的个人常用设备信息、网络身份识别信息和网络环境信息，包括设备ID（包括IOS广告标识符（IDFA）、国际移动设备识别码（IMEI）、匿名设备标识符(OAID)、国际移动用户识别码（IMSI））、设备类型和版本、系统版本、IP地址、MAC地址、应用ID、网络类型等信息。</p>

<p>4、在您使用游族网络游戏服务的过程中，我们会收集您的游戏日志信息，例如登录日志、商店购买日志、活动日志、公会日志、升级日志、聊天日志等，以便您能够在客户端查看您的游戏历史记录，同时会用于游戏运营统计分析、客服投诉处理、游戏反作弊分析，以及游戏体验优化提升。</p>

<p>5、当您在游族网络游戏中进行充值、支付时，我们会收集您的充值记录、消费记录等信息，以便您查询您的交易记录，以及在交易出现争议时，为客服人员提供信息以支持争议诉求的处理。同时，为了尽最大程度保护您的财产安全，我们会收集支付时的设备信息、网络身份识别信息和网络环境信息，以对高风险交易行为进行识别、二次确认或阻断。</p>

<p>6、为保障您的游戏账号安全，营造公平、健康及安全的游戏环境，我们会收集您的游戏应用信息、设备信息、网络身份识别信息、操作系统及进程信息、网络环境信息及游戏崩溃记录等信息，以用于打击破坏游戏公平环境或干扰、破坏游戏服务正常进行的行为（如外挂作弊等）。</p>

<p>7、当您在游戏聊天室中或论坛上通过文字、图片、语音、视频及其他方式发布信息或与其他玩家进行互动时，我们可能会收集您发送的上述互动信息内容，用于对色情、暴力、政治、辱骂、恶意广告等不当内容进行过滤和监测，以净化游戏环境，维护健康的上网环境。</p>

<p>8、为了提供游戏基础及扩展功能，我们在移动端嵌入了第三方SDK，可能会收集您的个人信息以确保功能的正常使用，服务功能及对应收集信息如下：</p>

<p>1) 社交分享：为了向您提供多个主流社交平台账号内的一键分享功能及第三方授权登录服务，通过调用系统相关接口，提供区域化的用户运营服务，该等第三方SDK会收集您的地理位置信息；</p>

<p>2) 快速登陆：为了支持三大运营商号码认证，向您提供一键验证功能，方便您快速注册与登录游戏，通过调用系统相关接口，第三方SDK会收集您的运营商信息、网络状态以及手机号码，若您不同意收集，则第三方SDK产品无法提供快速登录服务；</p>

<p>3) 玩家互动：为了向您提供通过语音、视频与其他游戏玩家互动的服务，在您授权同意后，游戏会访问您的麦克风、摄像头，为您提供语音聊天、直播互动等功能，第三方SDK可能会收集您的设备信息和网络环境信息；</p>

<p>4)智能推送：为了支持智能推送服务，我们使用了MobTech等第三方提供的SDK，通过调用系统相关接口，第三方SDK会收集您的地理位置等相关信息，若您不同意收集地理位置，则此第三方SDK将不再针对特定区域群体推送消息；</p>
