<p>5) 反欺诈保护：为保护您的网络信息安全，我们与第三方SDK北京数美时代科技有限公司（以下简称“数美”）合作向您提供反欺诈保护，为提供前述反欺诈服务，数美需收集您的相关网络与账号信息，具体请见数美科技隐私政策，链接如下：<a class="link-url" href="https://www.ishumei.com/legal/cn/privacy.html">https://www.ishumei.com/legal/cn/privacy.html</a></p>

<p>6) 广告归因：为向您提供广告分析服务并防止广告代理或广告平台作弊，从而更好地制定广告营销策略以提高您的游戏体验，您同意我们嵌入Appsflyer SDK为您提供服务, Appsflyer为实现前述功能会收集您的相关信息并提供安全合理的数据保护，具体请见Appsflyer隐私政策，链接如下：<a class="link-url" href="https://www.appsflyer.com/services-privacy-policy/">https://www.appsflyer.com/services-privacy-policy/</a></p>

<p>7) 其他：某些第三方SDK还可能会收集您的设备信息、网络身份识别信息、操作系统信息、网络环境信息用于产品分析、账号纠纷时确认归属人或营销信息推送，具体情况请参见SDK运营方的隐私政策或相关声明。</p>

<p>9、根据相关法律法规及国家标准，以下情形中，我们可能会在未经您授权或同意的情况下，收集、使用您的相关个人信息： </p>

