<p>为了履行法律法规及监管机构所规定的各项义务，在支持内外部的各类审核过程中，在签署保密协议的前提下，我们可能会向监管机构或审核机构提供匿名化处理后的个人信息、设备信息、交易信息等。</p>

<p>当我们要将信息用于本政策未载明的其它用途时，会事先征求您的同意。</p>

<p>当我们要将基于特定目的收集而来的信息用于其他目的时，会事先征求您的同意。</p>

<p class="title mt30">三、我们如何使用 Cookie 和同类技术</p>

<p>（一）Cookie</p>

<p>Cookie 是由用户访问的网站向浏览器发送的一小段文本，它通常包含标识符、站点名称以及一些号码和字符。它帮助浏览器记录有关访问活动的信息，例如会话信息、首选语言和其他一些设置。这样，用户在下次访问同样的网站时就能获得更轻松实用的访问体验。</p>

<p>我们使用Cookie以实现诸多目的，如记录用户会话信息、登陆和操作状态、默认语言配置等。使用Cookie将使您在使用游族网络的服务时获得更好的体验。</p>

<p>您同意我们收集并使用Cookie，我们不会将 Cookie 用于本政策所述目的之外的任何用途。您可根据自己的偏好管理或删除 Cookie。有关详情，请参见<a class="link-url" href="https://www.aboutcookies.org/">www.aboutcookies.org</a>。大部分网络浏览器都设有管理Cookie 的功能，如需详细了解如何更改浏览器设置，请访问相应浏览器的官方网站，如Internet Explorer、Google Chrome、Mozilla Firefox、Safari和Opera。</p>

<p>（二）网站信标和像素标签</p>

<p>除 Cookie 外，我们还会在网站上使用网站信标和像素标签等其他同类技术。例如，我们向您发送的电子邮件可能含有链接至我们网站内容的点击 URL。如果您点击该链接，我们则会跟踪此次点击，帮助我们了解您的产品或服务偏好并改善客户服务。网站信标通常是一种嵌入到网站或电子邮件中的透明图像。借助于电子邮件中的像素标签，我们能够获知电子邮件是否被打开。如果您不希望自己的活动以这种方式被追踪，则通过网址<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>联系我们，随时从我们的寄信名单中退订。</p>

<p class="title mt30">四、我们如何共享、转让、公开披露您的个人信息</p>

<p>（一）共享</p>

<p>提请您注意，在您注册或使用游族网络游戏服务时，将由游族网络与其关联公司（合称“游族集团”）整合所有资源为您提供服务，所以您的部分信息可能依据最小必要原则为提供相关服务在集团内部分享。我们承诺游族集团内所有公司将严格遵守本隐私政策的要求，受本隐私政策中所声明目的的约束。关联公司或附属公司若确有需要改变个人信息的处理目的，将再次征求您的授权同意。</p>

<p>我们不会与游族集团以外的任何公司、组织和个人分享您的个人信息，但以下情况除外：</p>

<p>1、在获取明确同意的情况下共享：获得您的明确同意后，我们会与其他方共享您的个人信息。</p>

<p>2、我们可能会根据法律法规规定，或按政府主管部门的强制性要求，对外共享您的个人信息。</p>

<p>3、与我们的附属公司共享：您的个人信息可能会与游族网络的附属公司、关联公司共享。我们只会共享必要的个人信息，且受本隐私政策中所声明目的的约束。附属公司如要改变个人信息的处理目的，将再次征求您的授权同意。</p>

<p>4、我们的附属公司包括但不限于：上海驰游信息技术有限公司、上海游娱信息技术有限公司、南京游族信息技术有限公司、上海游家信息技术有限公司、上海游族互娱网络科技有限公司、上海游数信息技术有限公司、上海游昆信息技术有限公司、上海游种信息技术有限公司、南京驰游信息技术有限公司、上海族生信息技术有限公司。</p>

<p>5、与授权合作伙伴共享：仅为实现本政策中声明的目的，我们的某些服务将由授权合作伙伴提供。我们可能会与合作伙伴共享您的某些个人信息，以提供更好的客户服务和用户体验。例如，在您上网购买我们的产品或服务时，我们必须与物流、支付、平台服务服务提供商共享您的个人信息才能安排送货、或者安排合作伙伴提供服务。我们仅会出于合法、正当、必要、特定、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息，确保符合最小化原则。我们的合作伙伴无权将共享的个人信息用于任何其他用途。目前，我们的授权合作伙伴包括以下类型： </p>

<p>1) 广告、分析服务类的授权合作伙伴。除非得到您的许可，否则我们不会将您的个人身份信息（指可以识别您身份的信息，例如姓名、手机号、身份证号码或电子邮箱，通过这些信息可以联系到您或识别您的身份）与提供广告、分析服务的合作伙伴分享。我们可能会向这些合作伙伴提供经过匿名化处理的信息，用于广告投放及运营分析，匿名性会确保相关信息不会识别您个人。</p>

<p>2) 供应商和服务提供商。我们可能会将信息发送给支持我们业务的供应商、服务提供商，如支付服务商、物流服务商等，以为您提供交易服务、邮递服务等。</p>

<p>3) 联合运营合作伙伴。在游戏联合运营时，我们可能会与联合运营的合作伙伴共享您的信息，包括您的个人基本信息、账户信息、交易信息等，以支持客服反馈问题的跟踪处理、财务对账等客户支持或运营活动。</p>

<p>对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的说明、本隐私政策以及其他任何相关的保密和安全措施来处理个人信息。</p>

<p>（二）转让</p>

<p>我们不会将您的个人信息转让给游族集团以外的任何公司、组织和个人，但以下情况除外：</p>

<p>1、在获取明确同意的情况下转让：获得您的明确同意后，我们会向其他方转让您的个人信息；</p>

<p>2、在涉及合并、收购、业务板块出售、破产清算时，如涉及到个人信息转让，我们会在要求新的持有您个人信息的公司、组织继续受此隐私政策的约束。如变更个人信息收集的范围或使用目的时，我们将要求该公司、组织重新向您征求授权同意。</p>

<p>（三）公开披露</p>

<p>我们仅会在以下情况下，公开披露您的个人信息：</p>

<p>1、获得您明确同意后；</p>

<p>2、基于法律的披露：在法律、法律程序、诉讼、政府监管或政府主管部门强制性要求的情况下，我们可能会公开披露您的个人信息。</p>

<p class="title mt30">五、我们如何保护您的个人信息</p>

<p>（一）我们已使用符合业界标准的安全防护措施保护您提供的个人信息，防止数据遭到未经授权访问、公开披露、使用、修改、损坏或丢失。我们会采取一切合理可行的措施，保护您的个人信息。例如，网络通信采用HTTPS协议进行加密保护；基于堡垒机的运维审计和访问控制，网络边界部署了防火墙、WAF（web应用防火墙）、抗DDOS流量清洗设备；敏感数据在数据库中通过非对称通信加密和严格的访问控制进行保护；在服务端的网络和终端层面部署了纵深安全防御体系，以免受恶意攻击；建立了完整的信息安全管理体系以规范日常的内部人员对数据的访问、使用、编辑、存储、共享、删除等活动，确保只有授权人员可以访问到受保护的信息；敏感数据访问、存储、使用的全生命周期监测预警；管理后台默认遮蔽和访问授权及审计控制；以及我们会举办信息安全和隐私保护培训课程及意识教育相关活动，加强员工对于保护个人信息重要性的认识。</p>

<p>（二）我们已经取得了以下认证：网络安全等级保护（三级）的备案和测评。</p>

<p>（三）我们的数据安全能力：应用层加密，网络层加密，数据库加密，网络访问异常监测，数据库访问异常监测等。</p>

<p>（四）我们会采取一切合理可行的措施，确保不收集无关的个人信息。我们只会在达成本政策所述目的所需的最短期限内保留您的个人信息，除非需要延长保留期或受到法律的允许。</p>

<p>（五）互联网并非绝对安全的环境，我们强烈建议您尽可能避免通过非加密的方式发送个人信息。请使用复杂密码（包含数字、大小写字母、特殊符号，并避免姓名、生日等信息），协助我们保证您的账号安全。</p>

<p>（六）我们将定期更新并公开安全风险、个人信息安全影响评估等报告的有关内容。您可通过访问我们的官方网站 <a class="link-url" href="https://www.youzu.com/">www.youzu.com</a> 获得。</p>

<p>（七）互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的任何信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将承担相应的法律责任。</p>

<p>（八）在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报个人信息安全事件的处置情况。</p>

<p class="title mt30">六、您的权利</p>

<p>按照中国相关的法律、法规、标准，以及其他国家、地区的通行做法，我们保障您对自己的个人信息行使以下权利：</p>

<p>（一）访问您的个人信息 </p>

<p>1、您有权访问您的个人信息，法律法规规定的例外情况除外。如果您想行使数据访问权，可以在登陆后，通过访问“游族首页-个人中心”自行访问。在个人中心，您可以查看账号信息，交易信息，虚拟财产信息，积分信息，站内信件等。</p>

<p>2、如果您无法通过上述方式访问这些个人信息，或希望访问您在使用我们的产品或服务过程中产生的其他个人信息，您可以通过“游族首页-服务中心”（网址：<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>）获得我们的联系方式，包括服务热线、自助服务、在线客服等，联系我们提出您的访问请求，我们将在15个工作日内回复您的访问请求。</p>

<p>（二）修改、更正您的个人信息 </p>

<p>1、您有权对您的个人信息进行修改，法律法规规定的例外情况除外。如果您想行使数据修改、更正权，可以在登陆后，通过访问“游族首页-个人中心”（网址：<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>）自行访问。在个人中心，您可以修改账号昵称、登陆密码、性别、QQ、学历、行业、收入、所在地、签名等信息。</p>

<p>2、当您发现我们处理的关于您的个人信息有错误而无法自行更正时，您有权要求我们做出更正。您可以通过“游族首页-服务中心”（网址：<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>）获得我们的联系方式，包括服务热线、自助服务、在线客服等，联系我们提出您的更正请求，我们将在15个工作日内回复您的更正请求。</p>

<p>（三）删除您的个人信息 </p>

<p>1、在以下情形中，您可以向我们提出删除个人信息的请求：</p>

<p>1) 如果我们处理个人信息的行为违反法律法规；</p>

<p>2) 如果我们收集、使用您的个人信息，却未征得您的同意；</p>

<p>3) 如果我们处理个人信息的行为违反了与您的约定；</p>

<p>4) 如果您不再使用我们的产品或服务，或您注销了账号；</p>

<p>5) 如果我们不再为您提供产品或服务。</p>

<p>2、若我们决定响应您的删除请求，我们还将同时通知从我们获得您的个人信息的实体，要求其及时删除，除非法律法规另有规定，或这些实体获得您的独立授权。</p>

<p>3、当您从我们的服务中删除信息后，受限于系统运营我们可能不会立即从备份系统中删除相应的信息，但会在备份更新时删除这些信息。</p>

<p>（四）改变您授权同意的范围 </p>

<p>1、游族网络在收集用户信息时遵循必要性原则，提供基本游戏服务功能需要一些基本的个人信息才能得以完成（见本政策“第一部分”）。对于额外收集的个人信息的收集和使用，您可以随时给予或收回您的授权同意。</p>

<p>2、您可以通过“游族首页-服务中心” 网址<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>，获得我们的联系方式，包括服务热线、自助服务、在线客服,，联系我们提出您的请求。</p>

<p>3、当您撤销同意后，我们将不再处理相应的个人信息。但您撤销同意的决定，不会影响此前基于您的授权而开展的个人信息处理。</p>

<p>4、如果您不想接受我们给您发送的邮件、短信商业广告，您可以随时根据随附广告内容的退订指引进行操作。</p>

<p>（五）个人信息主体注销账户</p>

<p>1、您可以通过访问“游族首页-个人中心”（网址：<a class="link-url" href="http://service.youzu.com/">http://service.youzu.com/</a>）自助提出用户注销申请，我们将在15个工作日内协助您注销账号。账号注销后，您将无法使用我们提供的服务。</p>

<p>2、您也可以通过“游族首页-服务中心”获得我们的联系方式，包括服务热线、自助服务、在线客服等，联系我们提出您的账号注销请求。</p>

<p>3、如果您使用关联的微信、QQ或微博账号作为游戏账号登录相关游戏，您需要注销微信、QQ或微博账号，请在微信、QQ或微博根据其指引进行注销。</p>

<p>4、在注销账户之后，我们将停止为您提供产品或服务，并依据您的要求，删除您的个人信息，法律法规另有规定的除外。</p>

<p>（六）个人信息主体获取个人信息副本 </p>

<p>1、您有权获取您的个人信息副本，您可以通过“游族首页-服务中心”获得我们的联系方式，包括服务热线、自助服务、在线客服等，联系我们提出您的请求。</p>

<p>（七）约束信息系统自动决策 </p>

<p>1、目前游族网络尚未使用信息系统自动决策功能。未来，如果在某些业务功能中，我们使用仅依据信息系统、算法等在内的非人工自动决策机制做出决定的机制，我们将为您提供关闭自动决策的功能选项。</p>

<p>（八）响应您的上述请求 </p>

<p>1、为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。</p>

<p>2、我们将在15个工作日内做出答复。若该等期限内因为客观原因的限制，确实无法给出答复的，我们将提前联系您延长答复期限，以给到您满意的答复。</p>

<p>3、如您不满意，还可以通过拨打“游族首页-服务中心”上所示的客服电话进行投诉。</p>

<p>4、对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情况收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际的请求，我们可能会予以拒绝。</p>

<p>5、在以下情形中，我们将无法响应您的请求：</p>

<p>1) 与个人信息控制者履行法律法规规定的义务相关的；</p>

<p>2) 与国家安全、国防安全直接相关的；</p>

<p>3) 与公共安全、公共卫生、重大公共利益直接相关的；</p>

<p>4) 与犯罪侦查、起诉、审判和执行判决等直接相关的；</p>

<p>5) 个人信息控制者有充分证据表明个人信息主体存在主观恶意或滥用权利的；</p>

<p>6) 出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</p>

<p>7) 响应个人信息主体的请求将导致个人信息主体或其他个人、组织的合法权益受到严重损害的；</p>

<p>8) 涉及商业秘密的。</p>

<p class="title mt30">七、我们如何处理儿童的个人信息</p>

<p>（一）我们高度重视儿童个人信息的保护问题，依照国家相关法律法规的规定，我们将不满 14 周岁的任何未成年人均视为儿童。游族网络主要面向成人。如果没有父母或监护人的同意，儿童不应创建自己的个人账户，对于经父母同意而收集儿童个人信息的情况，我们只会在受到法律允许且提供游戏必要、父母或监护人明确同意或者保护儿童所必要的情况下使用或公开披露此信息。</p>

<p>（二）我们会按照国家防沉迷政策要求，通过启用防沉迷系统保护未成年人（包括儿童）的合法权益。我们会通过实名身份等信息校验判断相关账号的实名信息是否为未成年人，进而决定是否将此账号纳入到防沉迷体系中。</p>

<p>（三）另外，我们还建立了家长监护平台。家长监护平台可以帮助家长或监护人随时了解孩子的游戏情况，设置消费限额以及每天的游戏时长和时段，协助儿童的家长或监护人保护儿童免受不良游戏习惯的伤害。</p>

<p>（四）我们收集、共享、转让、公开披露儿童的个人信息的范围同上述服务范围，但任何收集、存储、共享、转让、公开披露儿童的个人信息的行为均需事先经过儿童监护人的明示同意，若国家法律法规关于儿童个人信息保护出台新的政策限制针对儿童的个人信息的相关行为，我们将依照国家法律法规相应调整我们提供的收集、共享、转让、公开披露儿童的个人信息的范围。</p>

<p>（五）若您是儿童的法定监护人，请您关注您所监护的儿童是否是在取得您的授权同意之后使用游族网络的服务或提供其个人信息。如果儿童的监护人明确拒绝我们以上述方式向儿童提供服务，我们将依照法律规定及时删除我们收集、存储、使用的相关儿童个人信息，并停止向该儿童提供服务。</p>

<p>（六）我们收集到的儿童个人信息被定义为敏感信息，所有收集到的儿童个人信息均存储在中国大陆境内。为确保信息安全，我们采取加密等措施存储儿童个人信息。儿童个人信息会受到严格的技术保障措施的保护，例如，在收集儿童个人信息之后，我们会对其进行脱敏处理。此外，游族网络还建立了严格的数据安全管理流程，对于敏感数据的访问、使用进行了严格的限制，所有针对敏感信息的访问或权限申请都需要经过申请、审批并留存记录。</p>

<p>（七）我们有专人负责儿童个人信息保护，如果您对儿童个人信息保护有任何疑问或需求，您可以发邮件联系我司儿童信息保护的负责人邮箱ETYSBH@yoozoo.com，我们将在15个工作日内答复处理。</p>

<p>（八）就儿童个人信息而言，如其余条款与本协议第七条有冲突，以本协议第七条为准，本条未进行规定的，适用本协议其余条款。</p>

<p class="title mt30">八、您的个人信息如何在全球范围转移</p>

<p>原则上，我们在中华人民共和国境内收集和产生的个人信息，将存储在中华人民共和国境内。未经您的明示授权及相关主管机构的审批，我们不会将您的个人信息转移到境外管辖区。</p>

<p class="title mt30">九、本政策如何更新</p>

<p>1、我们的隐私政策可能变更。</p>

<p>2、未经您明确同意，我们不会削减您按照本隐私政策所应享有的权利。</p>

<p>3、我们会在本页面上发布对本政策所做的任何变更。</p>

<p>4、对于重大变更，我们还会提供更为显著的通知（包括对于某些服务，我们会通过电子邮件发送通知，说明隐私政策的具体变更内容）。</p>

<p>5、本政策所指的重大变更包括但不限于：</p>

<p>1) 我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</p>

<p>2) 我们在所有权结构、组织架构等方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；</p>

<p>3) 个人信息共享、转让或公开披露的主要对象发生变化；</p>

<p>4) 您参与个人信息处理方面的权利及其行使方式发生重大变化；</p>

<p>5) 我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生变化时；</p>

<p>6) 个人信息安全影响评估报告表明存在高风险时。</p>

<p>6、我们还会将本政策的旧版本存档，供您查阅。</p>

<p class="title mt30">十、如何联系我们</p>

<p>1、如果您对本隐私政策有任何疑问、意见或建议，您可以通过“游族首页-服务中心”获得我们的联系方式，包括服务热线、自助服务、在线客服等，联系我们提出您的要求，我们会在15个工作日内进行答复。</p>

<p>     如果您有任何疑问、意见或建议，请通过以下联系方式与我们联系：</p>

<p>     电 话：400-663-8866</p>

<p>2、如果您对我们的回复不满意，特别是认为我们的个人信息处理行为损害了您的合法权益，您还可以通过发送邮件至个人隐私信息违法问题举报专用邮箱pip@tc260.org.cn寻求解决方案。</p>
